<script>
	import https from '../api/https'
	import DescriptionPage from '../components/DescriptionPage'
	export default {
		components: {
			DescriptionPage,
		},
		name: 'Download',
		data: function(){
			return {
				errorPageActive: false,
				errorPageTitle: '',
				errorPageScript: '',
				errorPageBtnText: 'Back',

				bgWrapHeight: 200,
				moneySymbol: https.moneySymbol,
				helpBox: false,
				phoneType: 'Android',
				
				swiperOption2: {
					direction: 'vertical',
					loop: true,
					slidesPerView: 1,
					autoplay: {
						delay: 4000,
						stopOnLastSlide: false,
						disableOnInteraction: false,
					},
					allowTouchMove: false,
				},

				target: '',
				channel: '',
				assistantData: [],
				homeDetailData: null,
			}
		},
		created(){
			this.channel = this.$route.params.channel
			if(!this.channel) {
				if(!localStorage.getItem('user-token')) {
					this.errorPageTitle = 'Invalid argument'
					this.errorPageScript = 'Please check the parameters'
					this.errorPageBtnText = 'Home'
					this.errorPageActive = true
				}
			}
			let sysType = 'android'
			let u = navigator.userAgent
			let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
			let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
			if (isAndroid) {
				this.phoneType = 'Android'
				sysType = 'android'
			}
			if (isIOS) {
				this.phoneType = 'IOS'
				sysType = 'ios'
			}
			this.getHomeData(sysType)
			// this.setFileData()
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			this.bgWrapHeight = viwePortHeight
		},
		methods: {
			getHomeData: function(sysType){
				this.$toast.loading({
					message: 'Loading...',
					forbidClick: true,
					loadingType: 'spinner',
					icon: require('../assets/images/loadingIcon.png'),
					duration: 0,
					className:'toastLoading'
				});
				let params = {
					platform: sysType
				}
				https.fetchGet('/api/user/app',params).then((data) => {
					console.log(data)
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						if(res.data && res.data.file) {
							this.setFileData(res.data.file)
						}else {
							this.setFileData('')
						}
					}else {
						this.$toast.loading({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className:'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
						this.setFileData('')
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getHomeData()
					}
				})
			},
			setFileData(fileData){
				console.log(this.$route.params.channel)
				this.channel = this.$route.params.channel || ''
				this.id = localStorage.getItem('downloadID')
				let idStr = ''
				if(this.id) {
					idStr = '&id='+this.id
				}
				var dir = "/app/";
				var file = "YouLike-1.0.5.apk";
				if(fileData) {
					file = fileData
				}

				let channelStr = '';
				if(this.channel) {
					channelStr = '&channel='+this.channel
					this.target = dir + file.substring(0, file.length - 4) + "-" + this.channel + ".apk";
				}else {
					this.target = dir+file
				}
				if(this.channel.search('.apk') != -1) {
					console.log(111111)
					this.target = dir+this.channel
				}
				let params = 'event=access'+channelStr+idStr
				this.eventUpload(params)
			},
			openWindow(type,account) {
				let baseHelpUrl = ''
				if(type == 'whatsapp') {
					baseHelpUrl = "https://wa.me/" + account
				}else if(type == 'telegram'){
					baseHelpUrl = "https://t.me/" + account
				}
				window.open(baseHelpUrl)
			},
			eventUpload(params){
				console.log(params)
				// https.fetchPost('/api/report/stat?'+params).then((data) => {
				// 	let res = data.data
				// 	if(res.code == 0) {
				// 		localStorage.setItem('id',res.id)
				// 	}else if(res.code == -3 || res.code == -2) {
				// 		this.$router.push('/home')
				// 	}else {
				// 		this.$toast({
				// 			message: res.msg,
				// 			duration: 2000,
				// 			className: 'smallToast'
				// 		});
				// 	}
				// }).catch(err=>{
				// 	if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
				// 		this.NetWorkRetryTimes = 0
				// 	}else {
				// 		this.NetWorkRetryTimes = 0
				// 		this.NetWorkRetryTimes++
				// 		this.eventUpload()
				// 	}
				// 	console.log(err)
				// })
			},
			clickInstall(){
				let channelStr = ''
				if(this.channel) {
					channelStr = '&channel='+this.channel
				}
				let idStr = ''
				if(this.id) {
					idStr = '&id='+this.id
				}
				let params = 'event=apk'+channelStr+idStr
				this.eventUpload(params)
				return false
			},
			goPage(page){
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		}
	}
</script>
